* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-131921: #131921;
  --color-3d4149: #3b4149;
  --color-febd69: #febd69;
  --color-232f3e: #232f3e;
  --color-3b4149: #3b4149;
  --color-bf4800: #bf4800;
  --color-f5f5f7: #f5f5f7;
  --color-ededed: #ededed;
  --color-777777: #777777;
  --color-1c1c1b: #1c1c1b;
}

.section-heading {
  font-size: 26px;
  line-height: 32px;
  font-weight: 500;
  margin-bottom: 30px;
}

.card-wrapper {
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}

body {
  font-family: 'Rubik', sans-serif;
}

a {
  text-decoration: none;
  display: inline-block;
}

.form-control:focus {
  box-shadow: none;
  border-color: inherit;
}

.gap-5 {
  gap: 5px;
}

.gap-10 {
  gap: 10px;
}

.gap-15 {
  gap: 15px;
}

.gap-20 {
  gap: 20px;
}

.gap-30 {
  gap: 30px;
}

.button {
  background-color: var(--color-232f3e);
  color: white;
  font-size: 14px;
  padding: 13px 33px;
  border-radius: 30px;
}

.button:hover {
  background-color: var(--color-febd69);
  color: #131921;
}

/* Header Secstion */
.header-top-strip {
  border-bottom: 1px solid var(--color-3d4149);
  background-color: var(--color-131921);
}

.header-upper {
  background-color: var(--color-131921);
}

.header-bottom {
  background-color: var(--color-232f3e);

}

.header-upper .input-group-text {
  background-color: var(--color-febd69);
}

.header-upper.header-upper-links img {
  width: 35px;
  height: 35px;
}

.header-bottom .menu-links a,
.header-bottom .dropdown button {
  color: white;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  letter-spacing: 0.3;
  text-transform: uppercase;
}

.header-bottom .dropdown button::before {
  contain: url(../public/images/menu.svg);
}

.header-bottom .dropdown button:focus {
  box-shadow: none;
}

.header-bottom .dropdown-menu {
  background-color: var(--color-131921);
  width: 100%;
  transform: translate3d(0px, 57px, 0px) !important;
}

.header-bottom .dropdown-item {
  padding: 20px;
  margin-bottom: 3px;
  border-bottom: var(--color-3b4149);
}

.header-bottom .dropdown-item:hover {
  background-color: transparent;
  color: var(--color-febd69) !important;
}

/* Header Section End */

/* Footer start */

footer {
  background-color: var(--color-232f3e);
}

footer:not(:first-child) {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

footer .input-group-text {
  background-color: var(--color-232f3e);
  color: white;
  padding: 8px 17px;
}

/* Footer End */


/* Home page */

.main-banner-content {
  top: 15%;
  left: 5%;
}

.main-banner-content h4 {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: var(--color-bf4800);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}

.main-banner-content h5 {
  font-size: 52px;
  line-height: 64px;
  letter-spacing: -2px;
  font-weight: 500;
  /* padding: 0; */
  text-transform: none;
}

.main-banner-content p {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}

/* ---------- */

.small-banner-content {
  top: 25%;
  left: 10%;
}

.small-banner-content h4 {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: var(--color-bf4800);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}

.small-banner-content h5 {
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -2px;
  font-weight: 500;
  /* padding: 0; */
  text-transform: none;
}

.small-banner-content p {
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}

.small-banner {
  width: 49%;
}

/* Home wraper 2 */

.home-wrapper-2 {
  background-color: var(--color-f5f5f7);
}

/* Home wraper 2 End */

/* categories section */

.categories {
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}

.categories>div {
  width: 24%;
  padding: 10px 10px 10px 10px;
}

.categories>div>div {
  width: 60%;
}

.categories>div:nth-child(1),
.categories>div:nth-child(2),
.categories>div:nth-child(3),
.categories>div:nth-child(4) {
  border-bottom: 1px solid var(--color-ededed);
}

.categories>div:nth-child(1),
.categories>div:nth-child(2),
.categories>div:nth-child(3) {
  border-right: 1px solid var(--color-ededed);
}

.categories>div:nth-child(5),
.categories>div:nth-child(6),
.categories>div:nth-child(7) {
  border-right: 1px solid var(--color-ededed);
}

/* categories section end */

/* Home page End */



/* Blog part */
.blog-card {
  background-color: color-mix(in srgb, #eb5d1e, transparent 95%);
  /* box-shadow: 0 0 10px #0000001a; */
  /* padding: 20px; */
  border-radius: 10px;
}

.blog-card img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.blog-content {
  padding: 15px;
}

.blog-card p.date {
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  font-weight: 400;
  color: var(--color-777777);
  padding: 0;
}

.blog-content h5 {
  font-size: 18px;
  /* line-height: 30px;
  font-weight: 500; */
  color: var(--color-131921);
  /* margin: 0 0 10px; */
}

.blog-content p.desc {
  font-size: 13px;
  line-height: 22px;
  color: var(--color-777777);
  /* margin: 0; */
}

/* Blog part End */


/* product card start */

.product-card {
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}

.product-card .product-image {
  height: 270px;
}

.product-card .product-image img {
  object-fit: contain;
}


.product-card .product-image>img:last-child {
  display: none;
  /* transition: 0.3s; */
}

.product-card:hover .product-image>img:nth-child(1) {
  display: none;

}

.product-card:hover .product-image>img:last-child {
  display: block;

}

.product-card:hover .action-bar {
  right: 15px;
}


.product-card .product-details h6 {
  color: var(--color-bf4800);
  font-size: 13px;
}

.product-card .product-details h5 {
  color: var(--color-1c1c1b);
  font-size: 15px;
}

.product-card .product-details p.price {
  color: var(--color-1c1c1b);
  font-size: 16px;
}

.product-card p.description {
  color: var(--color-777777);
  font-size: 13px;
  margin-right: 20px;
}

.action-bar {
  top: 12%;
  right: -23px;
  transition: 0.3s;
}

.wishlist-icon {
  top: 3%;
  right: 16.5px;
  /* transition: 0.3s; */
}

/* Product Card End */


/* special Product start */

.special-product-card {
  padding: 20px 10px;
  border-radius: 10px;
  background-color: white;
}

.special-product-card .special-product-content h5 {
  color: var(--color-bf4800);
  font-size: 13px;
}

.special-product-card .special-product-content h6 {
  color: var(--color-1c1c1b);
  font-size: 15px;
}

.special-product-card .special-product-content .red-p {
  color: var(--color-bf4800);
  font-weight: 500;
  /* font-size: 16px; */
}

.special-product-card .special-product-content .cut-price {
  /* font-size: 13px; */
  line-height: 22px;
  color: var(--color-777777);
  /* margin: 0; */
}

/* special product end */



/* Famous section start */

.famous-card .famous-content {
  top: 10%;
  left: 5%;
}

.famous-card {
  border-radius: 10px;

  overflow: hidden;
  /* Add this to prevent the image from overflowing its container */
}

.famous-card img {
  object-fit: scale-down;
  /* Change this from 'contain' to 'scale-down' */
  border-radius: 10px;
  transition: transform 0.3s ease-in-out;
}

.famous-card img:hover {
  transform: scale(1.1);
}

.famous-content h5 {
  font-size: 16px;
  line-height: 10px;
  font-weight: 400;
  color: white;
  margin-bottom: 7px;
  text-transform: uppercase;
}

.famous-content h6 {
  font-size: 24px;
  line-height: 38px;
  font-weight: 500;
  color: white;

  /* margin-bottom: 10px; */
}

.famous-content p {
  font-size: 16px;
  line-height: 24px;
  color: white;
  font-weight: 100;
}

/* famous section end */

/* Our Store Start */

.filter-card {
  background-color: white;
  border-radius: 10px;
  padding: 15px 15px 25px;
}

.filter-title {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 20px;
}

.filter-card ul,
.filter-card .form-check-label {
  list-style-type: none;
  font-size: 13px;
  line-height: 30px;
  color: var(--color-777777);
  cursor: pointer;
}

.filter-card .form-check-input:focus {
  box-shadow: none;
  border-color: rgba(0, 0, 0, 0.25);
}

.filter-card .form-check-input:checked {
  background-color: var(--color-febd69);
  border-color: var(--color-febd69);

}


.sub-title {
  font-size: 14px;
  font-weight: 600;
  /* line-height: 22px; */
  color: var(--color-1c1c1b);
  margin-bottom: 20px;
  margin-top: 20px;
}

.colors {
  list-style-type: none;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.colors li {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  /* display: inline-block; */
  background-color: red;
}

.random-products h5 {
  font-size: 14px;
  margin-bottom: 8px;
}

.random-products:first-child {
  border-bottom: 1px solid var(--color-ededed);

}

.filter-sort-grid {
  padding: 10px;
  background-color: white;
  border-radius: 10px;
}

.grid img {
  height: 29px;
  width: 29px;
  background-color: #f2f2f2;
  padding: 5px;
  border-radius: 3px;
  cursor: pointer;
}

.gr-12 {
  width: 100%;
}

.gr-6 {
  width: 49%;
}

.gr-4 {
  width: 33.33%;
}

.gr-3 {
  width: 25%;
}

.gr-12 .product-card {
  display: flex;
  gap: 20px;
}

.gr-12 .product-card .product-image {
  width: 270px;
  height: auto;
}

.gr-12 .action-bar {
  top: 20%;
}



.gr-6 .product-image {
  width: 100%;
}

.gr-6 .product-image img {
  margin: auto;
  display: block;
}

/* Our Store End */

/* Contact Page Start */

.contact-inner-wrapper {
  padding: 20px;
  border-radius: 15px;
  background-color: white;
  gap: 15px;
}

.contact-inner-wrapper>div {
  width: 49%;
}

.contact-title {
  font-size: 26px;
  font-weight: 500;
  line-height: 35px;
  text-align: left;
  margin-top: 0;
}

.contact-inner-wrapper form .form-control {
  background-color: var(--color-f5f5f7);
  border-radius: 10px;
  border-color: transparent;
  padding: 10px;
}

.contact-inner-wrapper ul {
  list-style-type: none;
}

.contact-inner-wrapper a,
.contact-inner-wrapper p,
.contact-inner-wrapper span {
  color: var(--color-777777);
}

/* Contact Page End */